import React from 'react';
import './index.css';

class BackToTop extends React.Component {
  backToTop() {
    window.scroll({
      top: 0,
      behavior: 'smooth'
    });
  }

  render() {
    return (
      <button id='backToTop' onClick={ () => this.backToTop() }>
        <img
          src='data:image/png;base64,'
          alt=''
        />
      </button>
    );
  }
}

export default BackToTop;



